html,body {
  margin: 0;
}

.hero__menu {
  background-color: #304268;
  height: 44px;
  width: 44px;
  position: fixed;
  top: 0;
  left: 0;
}

.page__header {
  height: 100vh;
  background-image: url("/assets/images/cover.jpg");
  width: 100vw;
}

.hero__container {
  background-position: center;
  background-size: cover;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.hero__anonymous {
  margin: 0;
  width: 100%;
  color: #ffffff;
  font-size: 42px;
  font-family: 'Roboto Slab', serif;
  text-transform: uppercase;
  text-align: center;
}

.hero__anonymous-text {
  margin: 0;
  width: 100%;
}

.hero__habitasse {
  width: 100%;
  padding-top: 10px;
}

.hero__habitasse-text {
  width: 100%;
  color: #ffffff;
  font-size: 16px;
  font-family: 'Roboto', sans-serif;
  text-align: center;
}

.botton__down-container {
  padding-top: 45px;
}

.hero__button-down {
  background-color: #ffffff;
  background-size: 45px 45px;
  transform: scale(3);
  border-radius: 100%;
}

.hero__button-down-img {
  transform: scale(0.5);
}

.main__container {
  padding: 15px;
}

.main__block1-title {
  color: #8ccbea;
  font-family: "Roboto", sans-serif;
  font-size: 14px;
}

.main__block1-subtitle {
  font-family: "Roboto Slab", serif;
  font-size: 24px;
}

.main__block1-text {
  color: #4a4a4a;
  font-family: "Roboto", sans-serif;
  font-size: 18px;
}

.main__button-go {
  width: 75px;
  height: 45px;
  border-radius: 25%;
  border: 2px solid #f26002;
  background-color: white;
  color: #f26002;
}

.main__block2 {
  margin: 0;
  background-color: #eff9ff;
  list-style: none;
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: 1fr 1fr;
  grid-template-areas: "subtitle" "text";
  grid-gap: 5px;
}

.main__block2-title1 {
  font-family: "Roboto Slab", serif;
  font-size: 24px;
  text-align: center;
}

.subtitle {
  font-family: "Roboto Slab", serif;
  font-size: 18px;
  grid-column: span 1;
  text-align: center;
}

.text {
  font-family: "Roboto", sans-serif;
  font-size: 18px;
  grid-column: span 1;
  text-align: center;
}

.button__reasons-container {
  display: flex;
  justify-content: center;
}

.main__button-reasons {
  width: 75px;
  height: 45px;
  border-radius: 25%;
  background-color: #f26002;
  color: #ffffff;
}

.page__footer {
  background-color: #304268;
}

.footer__menu {
  list-style: none;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.footer__menu-item {
  color: #ffffff;
  font-family: "Roboto", sans-serif;
  font-size: 16px;
}

.footer__rrss {
  list-style: none;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  color: #ffffff;
  font-family: "Roboto", sans-serif;
  font-size: 16px;
}

.footer__copyrigth {
  padding: 15px;
  text-align: center;
  color: #ffffff;
  font-family: "Roboto", sans-serif;
  font-size: 14px;
}

.footer__love {
  padding: 15px 15px 60px 15px;
  text-align: center;
  color: #ffffff;
  font-family: "Roboto", sans-serif;
  font-size: 14px;
}

.heart {
  color: #f26002;
}
